body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
	background-color: whitesmoke !important;
}

.App {
	text-align: center;
	background-color: whitesmoke;
	padding: 20px 0px;
}

header .logoImage img {
	border-radius: 5px;
}

.explanation {
	text-align: justify;
}

.icons-container {
	-webkit-justify-content: center;
	        justify-content: center;
}

.icons-container .icon-container {
	display: -webkit-inline-flex;
	display: inline-flex;
	width: 100px;
	-webkit-justify-content: center;
	        justify-content: center;
}

.googleAnalyticsList {
	display: -webkit-flex;
	display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.googleAnalyticsList table {
	background-color: unset;
}

.googleAnalyticsList table th,
.googleAnalyticsList table td {
	text-align: center;
}

.googleAnalyticsFieldLoaded {
	border-radius: 5px;
	font-weight: 600;
	font-size: 18px;
}

.googleAnalyticsFieldLoaded.googleAnalyticsFieldBad {
	background-color: #fe8282;
	color: red;
}

.googleAnalyticsFieldLoaded.googleAnalyticsFieldGood {
	background-color: #8afe82;
}

@-webkit-keyframes textElementNotLoaded {
	0%   {background-color: #c4c4c4;}
	50%  {background-color: white;}
	100%  {background-color: #c4c4c4;}
  }

@keyframes textElementNotLoaded {
	0%   {background-color: #c4c4c4;}
	50%  {background-color: white;}
	100%  {background-color: #c4c4c4;}
  }

.textElementNotLoaded{
	width: 100px;
	height: 25px;
	background-color: #c4c4c4;
	-webkit-animation-name: textElementNotLoaded;
	        animation-name: textElementNotLoaded;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
	border-radius: 3px;
}
